* {
  --color-for-text: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  line-height: 60px;
  display: flex;
}

.img-logo {
  display: block;
}

.ul-nav {
  gap: 20px;
  display: flex;
}

.li-item {
  list-style: none;
}

.a-link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.a-link:hover {
  color: var(--color-for-text);
}

.is-active {
  color: var(--color-for-text);
  width: 100%;
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--color-for-text);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

[data-qa="hover"]:hover {
  color: var(--color-for-text);
}
